var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._m(0),_vm._v(" "),_vm._m(1),_vm._v(" "),_c('PostTag',{attrs:{"posttime":_vm.postTime}})],1)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('ul',{staticClass:"title-list"},[_c('li',[_c('h1',{staticClass:"title"},[_vm._v("D3 V4.0学习笔记")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"main-body"},[_c('div',{staticClass:"part",attrs:{"id":"part1"}},[_c('div',{staticClass:"title-lv1"},[_vm._v("1、D3V4.0画基本图代码")]),_vm._v(" "),_c('p',{staticClass:"descrp"},[_vm._v("D3V4.0画基本图代码")]),_vm._v(" "),_c('pre',{staticClass:"code-text"},[_vm._v("          "),_c('code',[_vm._v("\n           import '../../styles/common.less';\nimport '../../styles/index.less';\n/*import 'bootstrap';\nimport 'bootstrap/dist/css/bootstrap.css';*/\nimport * as d3 from \"d3\";\n\nclass Index {\n    /**\n     * test 无参考意义\n     * */\n    saySomething() {\n        // $('[data-toggle=\"popover\"]').popover();\n\n        // d3.select('.dtest')\n        //     .selectAll('p')\n        //     .text('yyyy')\n        //     .style('color','red')\n        //     .style('font-size','34px')\n        // var str=\"China\";\n        // d3.select('.dtest')\n        //     .selectAll('p')\n        //     .datum(str)\n        //     .text(function (d,i) {\n        //         return \"第\"+ i +\" 个元素绑定的数据是\"+ d;\n        //     })\n        var dataset = [1, 2, 3];\n        /* d3.select('.dtest')\n             .append('p')\n             .text('last append')*/\n        d3.select('.dtest')\n            .selectAll('p')\n            .data(dataset)\n            .text(function (d, i) {\n                return d;\n            })\n        /* d3.select('.dtest')\n             .insert('p','#hahh')\n             .text('insert')*/\n\n        /* d3.select('body').select('#hahh')\n             .remove();*/\n    }\n    /**\n     * test 无参考意义\n     * */\n    drawBar() {\n        let dataset = [250, 210, 170, 130, 90];\n        let height = 300;\n        let width = 300;\n        let svg = d3.select(\"body\")\n            .append(\"svg\")\n            .attr('width', width)\n            .attr('height', height)\n        let reactHeight = 25;\n\n        let linear = d3.scaleLinear()\n            .domain([0, d3.max(dataset)])\n            .range([0, 250]);\n\n        let axis = d3.axisBottom()\n            .scale(linear)\n            .ticks(7)\n\n        svg.append('g')\n            .attr(\"class\", \"axis\")\n            .attr(\"transform\", \"translate(20,130)\")\n            .call(axis);\n        /* svg.selectAll(\"rect\")\n             .data(dataset)\n             .enter()\n             .append(\"rect\")\n             .attr('x',20)\n             .attr('y',function (d,i) {\n                 return i*reactHeight;\n             })\n             .attr(\"width\",function (d) {\n                 return d;\n             })\n             .attr(\"height\",reactHeight-2)\n             .attr('fill',\"red\")*/\n        svg.selectAll('rect')\n            .data(dataset)\n            .enter()\n            .append('rect')\n            .attr('x', 20)\n            .attr('y', function (d, i) {\n                return i * reactHeight;\n            })\n            .attr('width', function (d) {\n                return linear(d);\n            })\n            .attr(\"height\", reactHeight - 2)\n            .attr('fill', \"red\");\n\n    }\n    /**\n     * 测试线性比例尺\n     * */\n    testScaleLiner() {\n        let dataset = [1.2, 2.4, 0.9, 1.5, 3.3];\n        let max = d3.max(dataset);\n        let min = d3.min(dataset);\n\n        console.log(d3)\n\n        var linear = d3.scaleLinear()\n            .domain([min, max])\n            .range([0, 300]);\n        console.log(linear(2.5))\n    }\n    /**\n     * 测试序数比例尺\n     * */\n    testOridinal() {\n        let index = [0, 1, 2, 3, 4]\n        let color = ['red', 'blue', 'green', 'yellow', 'black'];\n\n        let oridinal = d3.scaleOrdinal()\n            .domain(index)\n            .range(color);\n\n        console.log(oridinal(2))\n    }\n\n    /**\n     * 画柱状图\n     * */\n    testHoldBars() {\n        let width = 400;\n        let height = 400;\n        let svg = d3.select('body')\n            .append('svg')\n            .attr(\"width\", width)\n            .attr(\"height\", height);\n\n        let padding = {\n            left: 30,\n            right: 30,\n            top: 30,\n            bottom: 20\n        }\n\n        let dataset = [10, 20, 30, 40, 33, 24, 12, 5];\n        let temprange = d3.range(dataset.length);\n\n        let xScale = d3.scaleLinear()\n            .domain([0, temprange.length - 1])\n            .range([0, width - padding.left - padding.right]);\n\n        let yScale = d3.scaleLinear()\n            .domain([0, d3.max(dataset)])\n            .range([height - padding.top - padding.bottom, 0]);\n\n        let xAxis = d3.axisBottom()\n            .scale(xScale);\n\n        let yAxis = d3.axisLeft()\n            .scale(yScale);\n\n        let rectPadding = 4;\n        let rects = svg.selectAll('.MyRect')\n            .data(dataset)\n            .enter()\n            .append(\"rect\")\n            .attr('class', 'MyRect')\n            .attr('transform', 'translate(' + padding.left + ',' + padding.top + ')')\n            .attr('x', function (d, i) {\n                return xScale(i) + rectPadding / 2;\n                ///return xScale(i);\n                // return scaleBand(i);\n            })\n            .attr('y', function (d) {\n                //return yScale(d);\n                var min = yScale.domain()[0];\n                return yScale(min);\n\n            })\n            .attr('fill', 'steelblue')\n            .attr('width', 20)\n            .attr(\"height\", function (d) {\n                return 0;\n            })\n            .transition()\n            .delay(function (d, i) {\n                return i * 200;\n            })\n            .duration(2000)\n            .ease(d3.easeCubicInOut)\n            .attr(\"y\", function (d) {\n                return yScale(d);\n            })\n            .attr(\"height\", function (d) {\n                return height - padding.top - padding.bottom - yScale(d);\n            });\n\n        var texts = svg.selectAll(\".MyText\")\n            .data(dataset)\n            .enter()\n            .append(\"text\")\n            .attr(\"class\", \"MyText\")\n            .attr(\"transform\", \"translate(\" + padding.left + \",\" + padding.top + \")\")\n            .attr(\"x\", function (d, i) {\n                return xScale(i) + rectPadding / 2;\n            })\n            .attr(\"dx\", function (d, i) {\n                return 0;\n            })\n            .attr(\"dy\", function (d) {\n                return 20;\n            })\n            .text(function (d) {\n                return d;\n            })\n            .attr(\"y\", function (d) {\n                var min = yScale.domain()[0];\n                return yScale(min);\n            })\n            .transition()\n            /*.ease('bounce')*/\n            .delay(function (d, i) {\n                return i * 200;\n            })\n            .duration([2000])\n            .ease(d3.easeLinear)\n            .attr(\"y\", function (d) {\n                return yScale(d);\n            });\n\n        //添加x轴\n        svg.append(\"g\")\n            .attr(\"class\", \"axis\")\n            .attr(\"transform\", \"translate(\" + padding.left + \",\" + (height - padding.bottom) + \")\")\n            .call(xAxis);\n\n        //添加y轴\n        svg.append(\"g\")\n            .attr(\"class\", \"axis\")\n            .attr(\"transform\", \"translate(\" + padding.left + \",\" + padding.top + \")\")\n            .call(yAxis);\n\n        let svgDom = svg.node();\n\n        d3.select('svg')\n            .selectAll('.MyRect')\n            /*.on('mouseover',function(d,i){\n                let x = d3.mouse(svgDom)[0]+20;\n                let y = d3.mouse(svgDom)[1];\n                d3.select('.popup')\n                    .style('left',x+'px')\n                    .style('top',y+'px')\n                    .style('display','block')\n                    .select('.no')\n                    .text(d)\n            })\n           .on('mouseout',function (d,i) {\n               d3.select('.popup')\n                   .style('display','none')\n           })*/\n            .on(\"mouseover\", function (d, i) {\n                d3.select(this)\n                    .attr(\"fill\", \"yellow\");\n            })\n            .on(\"mouseout\", function (d, i) {\n                d3.select(this)\n                    .transition()\n                    .duration(500)\n                    .attr(\"fill\", \"steelblue\");\n            });\n    }\n\n    /**\n     * 画饼图\n     * */\n    drawPie() {\n        let width = 400;\n        let height = 400;\n        /*提供画布*/\n        let svg = d3.select('body')\n            .append('svg')\n            .attr('width', width)\n            .attr('height', height);\n        /*提供数据*/\n        let dataset = [10, 20, 30];\n\n        /*提供布局，数据转换*/\n        let pie = d3.pie();\n        let piedata = pie(dataset);\n\n        console.log(piedata);\n\n        let color = ['green', 'red', 'black'];\n        let outRadius = 150;\n        let innerRadius = 80;\n        /*弧生成器*/\n        let arc = d3.arc()\n            .innerRadius(innerRadius)\n            .outerRadius(outRadius);\n        let arcOver = d3.arc()\n            .innerRadius(innerRadius)\n            .outerRadius(outRadius + 10);\n\n        /* arc 可以当做一个函数使用，把 piedata 作为参数传入，即可得到路径值。*/\n\n        /* svg中添加相应的足够数量的组元素 g*/\n        let arcs = svg.selectAll('g')\n            .data(piedata)\n            .enter()\n            .append('g')\n            .style('cursor', 'pointer')\n            .attr(\"transform\", \"translate(\" + (width / 2) + \",\" + (width / 2) + \")\");\n        /*在g中生成path*/\n        arcs.append('path')\n            .attr('fill', function (d, i) {\n                return color[i];\n            })\n            .attr('d', function (d) {\n                return arc(d);\n            })\n            .on('mouseover', function (d, i) {\n                d3.select(this).attr('d', function (d) {\n                    return arcOver(d);\n                })\n            })\n            .on('mouseout', function (d, i) {\n                d3.select(this).attr('d', function (d) {\n                    return arc(d);\n                });\n            });\n        /*在g中生成tex*/\n        arcs.append('text')\n            .attr('transform', function (d) {\n                return 'translate(' + arc.centroid(d) + ')';\n            })\n            .attr('text-anchor', 'middle')\n            .attr('fill', '#fff')\n            .text(function (d) {\n                return '' + d.data + ',' + ((d.data / 60).toFixed(2)) * 100 + '%';\n            })\n    }\n\n    /**\n     * 画力图\n     * */\n    drawForce() {\n        var graph = {\n            \"nodes\": [\n                {name: '桂林'},\n                {name: '广州'},\n                {name: '南京'},\n                {name: '杭州'},\n                {name: '北京'},\n                {name: '上海'},\n                {name: '深圳'},\n                {name: '福州'},\n                {name: '厦门'},\n                {name: '三明'},\n                {name: '长沙'},\n                {name: '岳阳'},\n                {name: '香港'}\n            ],\n            \"links\": [\n                {\"source\": 0, \"target\": 1},\n                {\"source\": 1, \"target\": 2},\n                {\"source\": 2, \"target\": 0},\n                {\"source\": 1, \"target\": 3},\n                {\"source\": 3, \"target\": 2},\n                {\"source\": 3, \"target\": 4},\n                {\"source\": 4, \"target\": 5},\n                {\"source\": 5, \"target\": 6},\n                {\"source\": 5, \"target\": 7},\n                {\"source\": 6, \"target\": 7},\n                {\"source\": 6, \"target\": 8},\n                {\"source\": 7, \"target\": 8},\n                {\"source\": 9, \"target\": 4},\n                {\"source\": 9, \"target\": 11},\n                {\"source\": 9, \"target\": 10},\n                {\"source\": 10, \"target\": 11},\n                {\"source\": 11, \"target\": 12},\n                {\"source\": 12, \"target\": 10}\n            ]\n        }\n        var width = 960,\n            height = 500;\n        var svg = d3.select(\"body\").append(\"svg\")\n            .attr(\"width\", width)\n            .attr(\"height\", height);\n\n\n        var force = d3.forceSimulation()\n            .force(\"charge\", d3.forceManyBody().strength(-100))\n            .force(\"center\", d3.forceCenter(width / 2, height / 2))\n            .on(\"tick\", tick);\n\n        var forceLink = d3.forceLink(graph.links);\n        force\n            .nodes(graph.nodes)\n            .force(\"link\", forceLink);\n        forceLink.distance(20);\n        /**\n         * 画线\n         * */\n        var link = svg.selectAll(\".link\")\n            .data(graph.links)\n            .enter()\n            .append(\"line\")\n            .attr(\"class\", \"link\");\n\n        /**\n         * 画圆\n         * */\n        var length = graph.nodes.length.toString();\n\n        var color = d3.schemeCategory20;\n        console.log(color)\n        var node = svg.selectAll(\".node\")\n            .data(graph.nodes)\n            .enter()\n            .append(\"circle\")\n            .attr(\"class\", \"node\")\n            .attr(\"r\", 12)\n            .style('fill', function (d, i) {\n                return color[i];\n            })\n            // .on(\"dblclick\", dblclick)\n            .call(d3.drag()\n                .on(\"start\", dragstart)\n                .on(\"drag\", dragged));\n        /**\n         * 画文字\n         * */\n        var svg_texts = svg.selectAll(\"text\")\n            .data(graph.nodes)\n            .enter()\n            .append(\"text\")\n            .style(\"fill\", \"black\")\n            .attr(\"dx\", 0)\n            .attr(\"dy\", 0)\n            .text(function (d) {\n                return d.name;\n            });\n\n        console.log('graph.nodes:', graph.nodes, graph.links);\n\n        function tick() {\n            /* console.log('tick handler');*/\n            link.attr(\"x1\", function (d) {\n                return d.source.x;\n            })\n                .attr(\"y1\", function (d) {\n                    return d.source.y;\n                })\n                .attr(\"x2\", function (d) {\n                    return d.target.x;\n                })\n                .attr(\"y2\", function (d) {\n                    return d.target.y;\n                });\n\n            node.attr(\"cx\", function (d) {\n                return d.x;\n            })\n                .attr(\"cy\", function (d) {\n                    return d.y;\n                });\n\n            //更新文字坐标\n            svg_texts.attr(\"x\", function (d) {\n                return d.x;\n            })\n                .attr(\"y\", function (d) {\n                    return d.y;\n                });\n        }\n\n        function dragstart(d) {\n            console.log(d3.event.active);\n            if (!d3.event.active) {\n                force.alphaTarget(.1).restart();\n            }\n            d.fx = d.x;\n            d.fy = d.y;\n        }\n\n        function dragged(d) {\n            d.fx = d3.event.x;\n            d.fy = d3.event.y;\n        }\n\n        // function dblclick(d) {\n        //   d3.select(this).classed(\"fixed\", d.fixed = false);\n        //   console.log(\"dblclick\"+d.fixed);\n        // }\n        /* function dragended(d){\n             // force.stop();\n             if(!d3.event.active){\n                 force.alphaTarget(0);\n             }\n             // d.fx = null;\n             // d.fy = null;\n         }*/\n    }\n\n    /**\n     * 画炫图\n     * */\n    drawCord() {\n        let city_name = [\"北京\", \"上海\", \"广州\", \"深圳\", \"香港\"];\n\n        let population = [\n            [1000, 3045, 4567, 1234, 3714],\n            [3214, 2000, 2060, 124, 3234],\n            [8761, 6545, 3000, 8045, 647],\n            [3211, 1067, 3214, 4000, 1006],\n            [2146, 1034, 6745, 4764, 5000]\n        ];\n        let cord_layout = d3.chord();\n\n        cord_layout.padAngle(0.03)\n            .sortSubgroups()\n        let layout_test = cord_layout(population);\n\n        /*   console.log(layout_test,'----------------');*/\n        let groups = layout_test.groups;\n        let chords = layout_test.splice(0, layout_test.length);\n        console.log('grous，chords', groups, chords);\n\n        let width = 600;\n        let height = 600;\n\n        let innerRadius = width / 2 * 0.7;\n        let outRadius = innerRadius * 1.1;\n        let color20 = d3.schemeCategory20;\n\n        let svg = d3.select('body')\n            .append('svg')\n            .attr('width', width)\n            .attr('height', height)\n            .append('g')\n            .attr(\"transform\", \"translate(\" + width / 2 + \",\" + height / 2 + \")\");\n\n        var outer_arc = d3.arc()\n            .innerRadius(innerRadius)\n            .outerRadius(outRadius);\n\n        var g_outer = svg.append(\"g\");\n        /* console.log(outer_arc,'test')*/\n        g_outer.selectAll(\"path\")\n            .data(groups)\n            .enter()\n            .append(\"path\")\n            .style(\"fill\", function (d) {\n                return color20[d.index];\n            })\n            .style(\"stroke\", function (d) {\n                return color20[d.index];\n            })\n            .attr(\"d\", outer_arc);\n\n        g_outer.selectAll(\"text\")\n            .data(groups)\n            .enter()\n            .append(\"text\")\n            .each(function (d, i) {\n                d.angle = (d.startAngle + d.endAngle) / 2;\n                d.name = city_name[i];\n            })\n            .attr(\"dy\", \".35em\")\n            .attr(\"transform\", function (d) {\n                return \"rotate(\" + ( d.angle * 180 / Math.PI ) + \")\" +\n                    \"translate(0,\" + -1.0 * (outRadius + 10) + \")\" +\n                    ( ( d.angle > Math.PI * 3 / 4 && d.angle < Math.PI * 5 / 4 ) ? \"rotate(180)\" : \"\");\n            })\n            .text(function (d) {\n                return d.name;\n            });\n        /**\n         *\n         * 画连接的彩带\n         *v4有改动\n         * */\n        let ribbon = d3.ribbon()\n            .radius(innerRadius);\n\n        svg.append('g')\n            .attr('class', 'chord')\n            .selectAll('path')\n            .data(chords)\n            .enter()\n            .append('path')\n            .attr('d', ribbon)\n            .style('fill', function (d) {\n                return color20[d.source.index]\n            })\n            .style('opcity', 1)\n            .on('mouseover', function (d, i) {\n                d3.select(this)\n                    .style('fill', 'yellow')\n            })\n            .on('mouseout', function (d, i) {\n                d3.select(this)\n                    .transition()\n                    .duration(1000)\n                    .style('fill', color20[d.source.index]);\n            })\n\n    }\n\n    /**\n     * 画集群图 和树状图 两个图几乎一样；\n     * */\n    drawCluster() {\n        let width = 600;\n        let height = 600;\n\n        let svg = d3.select('body')\n            .append('svg')\n            .attr('width', width)\n            .attr('height', height);\n        let pathg=svg\n            .append(\"g\")\n            .attr(\"transform\", \"translate(40,0)\");\n        let nodeg=svg\n            .append('g')\n            .attr('class','nodes-group')\n            .attr(\"transform\", \"translate(40,0)\");\n\n        let text = svg\n            .append('g')\n            .attr('class','text-group')\n            .attr(\"transform\", \"translate(40,0)\");\n\n        /*var diagonal = d3.svg.diagonal()\n            .projection(function(d) { return [d.y, d.x]; });*/\n        /**\n         * 集群图\n         * */\n       /* let cluster = d3.cluster()*/\n       /**\n        *\n        * 树状图*/\n        let cluster =d3.tree()\n            .size([width, height - 200]);\n\n        function linkFun(d) {\n            return \"M\" + d.source.y + \",\" + d.source.x\n                + \"C\" + (d.source.y + d.target.y) / 2 + \",\" + d.source.x\n                + \" \" + (d.source.y + d.target.y) / 2 + \",\" + d.target.x\n                + \" \" + d.target.y + \",\" + d.target.x;\n        }\n\n        // console.log(cluster)\n        d3.json('../../assets/json/area.json?t='+new Date().getTime(), function (error, root) {\n            console.log(root);\n            let nodes = d3.hierarchy(root);\n            let haveDeal = cluster(nodes);\n\n            /**\n             * 所有节点\n             * */\n            let lastAfterEdit = haveDeal.descendants();\n            console.log(\"lastAfterEdit:\",lastAfterEdit);\n\n            /**\n             * 所有连线\n             * */\n            let links = nodes.links();\n\n            /***    画连线*/\n            var link = pathg.selectAll(\".link\")\n                .data(links)\n                .enter()\n                .append(\"path\")\n                .attr(\"class\", \"link\")\n                .attr(\"d\", d3.linkHorizontal()\n                    .x(function(d) { return d.y; })\n                    .y(function(d) { return d.x; }));\n           /* var circles=nodeg.selectAll('.circle')*/\n          /*  var pieg=nodeg.selectAll('g')\n                .data(lastAfterEdit)\n                .enter()\n                .append(\"g\")\n                .attr('x',function (d) {\n                    return d.x;\n                })\n                .attr('y',function (d) {\n                    return d.y;\n                })\n                .attr(\"transform\", \"translate(\" + (width / 2) + \",\" + (width / 2) + \")\")\n                .attr('class','pieItem')\n                .append('path')*/\n\n                /**\n                 * 画圆圈\n                 * */\n             var circles=nodeg.selectAll('.circle')\n                .data(lastAfterEdit)\n                .enter()\n                .append(\"circle\")\n                .attr('cx',function (d) {\n                    return d.y;\n                })\n                .attr('cy',function (d) {\n                    return d.x;\n                })\n                .attr(\"r\", 10);\n\n             /** 画文本*/\n           var texts = text\n               .selectAll('text')\n               .data(lastAfterEdit)\n               .enter()\n               .append('text')\n               .attr('x',function (d) {\n                   return d.y;\n               })\n               .attr('y',function (d) {\n                   return d.x;\n               })\n               .text(function (d) {\n                   return d.data.name;\n               })\n               .style(\"text-anchor\", function (d) {\n                   return d.children ? \"end\" : \"start\";\n               })\n               .attr(\"dx\", function (d) {\n                   return d.children ? -10 : 10;\n               })\n               .attr('dy',function (d) {\n                   return 5;\n\n               })\n        })\n    }\n    /**\n     * 打包图\n     *\n     * */\n    drawPack(){\n        let width = 800;\n        let height = 800;\n\n        let svg = d3.select('body')\n            .append('svg')\n            .attr('width', width)\n            .attr('height', height)\n            .attr('class','pack');\n\n        d3.json('../../assets/json/city.json?t='+new Date().getTime(), function (error, root) {\n            console.log(root);\n            var hierarchyData = d3.hierarchy(root);\n            hierarchyData.sum(function(d) {\n                console.log(d)\n                return d.value;\n            });\n            var pack = d3.pack().size([width, height]).padding(3);\n\n            console.log('hierarchyData',hierarchyData);\n\n            var root = pack(hierarchyData);\n\n            console.log('root:',root);\n            var nodes = root.descendants();\n            console.log('nodes:',nodes);\n\n            var gs = d3.select('svg')\n                .selectAll('g')\n                .data(nodes)\n                .enter()\n                .append('g')\n                .attr(\"transform\", function(d) {\n                    return `translate(${d.x}, ${d.y})`;\n                })\n            gs.append('circle')\n                .attr('r', function(d) {\n                    return d.r;\n                })\n                .style(\"fill\",\"rgb(31, 119, 180)\")\n                .attr(\"fill-opacity\",\"0.4\")\n                .on(\"mouseover\",function(d,i){\n                    d3.select(this)\n                        .style(\"fill\",\"#D7FAE1\");\n                })\n                .on(\"mouseout\",function(d,i){\n                    d3.select(this)\n                        .style(\"fill\",\"rgb(31, 119, 180)\");\n                });\n\n            gs.filter(function(d) {\n                return !d.children;\n            })\n            .append('text')\n            .style('fill','#fff')\n            .text(function(d) {\n                return d.data.name;\n            })\n\n\n        });\n    }\n    /**\n     *画地图\n     *\n     * */\n    drawMap(){\n        let width = 950;\n        let height = 750;\n\n        /** 颜色插入暂时还没用到*/\n        var a = 'rgb(0, 0, 0)';\n        var b = 'rgb(100, 100, 100)';\n        var interpolator = d3.interpolateRgb(a, b);\n         // 结果为 rgb(50, 50, 50)\n        let tempColor=[];\n        for (let i=0;i<2;i++){\n            let co = d3.schemeCategory20;\n            let length = co.length;\n            for(let j=0;j<<>length;j++){\n                tempColor.push(co[j]);\n            }\n        }\n\n        let svg = d3.select('body')\n            .append('svg')\n            .attr('width', width)\n            .attr('height', height)\n            .attr('class','pack')\n            .attr(\"viewBox\", \"0 0 \" + width + \" \" + height)\n            .call(d3.zoom().scal